<template>
  <div class="right">
    <cpntShow>
      <template slot="cpnt">DyMessage</template>
      <template slot="more">
          <h2 class="showTitle">duration持续时间</h2>
          <button @click="SMduration1000">1000ms</button>
          <button @click="SMduration2000">2000ms</button>
          <button @click="SMduration3000">3000ms</button>
          <h2 class="showTitle">position显示位置</h2>
          <button @click="SMpositionLeft">左边</button>
          <button @click="SMpositionCenter">中间</button>
          <button @click="SMpositionRight">右边</button>
          <h2 class="showTitle">type消息类型</h2>
          <button @click="SMtypeSuccess">success</button>
          <button @click="SMtypeError">error</button>
          <button @click="SMtypeWarning">warning</button>
          <button @click="SMtypeInfo">info</button>
      </template>

    </cpntShow>
  </div>
</template>

<script>
import cpntShow from '@/components/cpntShow.vue'
export default {
  components:{
    cpntShow
  },
  methods: {
    SMduration1000(){
      this.$message({
        message: '1000ms',
        duration: 1000,
        autoClose: true,
        position: 'right',
        type: 'success'
      })
    },
    SMduration2000(){
      this.$message({
        message: '2000ms',
        duration: 2000,
        autoClose: true,
        position: 'right',
        type: 'success'
      })
    },
    SMduration3000(){
      this.$message({
        message: '3000ms',
        duration: 3000,
        autoClose: true,
        position: 'right',
        type: 'success'
      })
    },
    SMpositionLeft () {
      this.$message({
        message: '我是一个message 消息提示',
        duration: 1000,
        autoClose: true,
        position: 'left',
        type: 'info'
      })
    },
    SMpositionCenter () {
      this.$message({
        message: '我是一个message 消息提示',
        duration: 1000,
        autoClose: true,
        position: 'center',
        type: 'info'
      })
    },
    SMpositionRight () {
      this.$message({
        message: '我是一个message 消息提示',
        duration: 1000,
        autoClose: true,
        position: 'right',
        type: 'info'
      })
    },
    SMtypeSuccess () {
      this.$message({
        message: 'success消息提示',
        duration: 1000,
        autoClose: true,
        position: 'center',
        type: 'success'
      })
    },
    SMtypeError () {
      this.$message({
        message: 'error 消息提示',
        duration: 1000,
        autoClose: true,
        position: 'center',
        type: 'error'
      })
    },
    SMtypeWarning () {
      this.$message({
        message: 'warning 消息提示',
        duration: 1000,
        autoClose: true,
        position: 'center',
        type: 'warning'
      })
    },
    SMtypeInfo () {
      this.$message({
        message: 'info 消息提示',
        duration: 1000,
        autoClose: true,
        position: 'center',
        type: 'info'
      })
    },
  }
}
</script>

<style scoped>
button {
  width: 100px;
  height: 50px;
  background-color: antiquewhite;
  border-radius: 15px;
  border: none;
  margin:10px;
  margin-right: 0px;
}
</style>